.tui-skeleton {
  position: relative;
  color: transparent !important;
  background: transparent !important;
  pointer-events: none;
  user-select: none;
}
.tui-skeleton::after {
  position: absolute;
  top: 0;
  left: 0;
  inline-size: 100%;
  block-size: 100%;
  content: '';
  background-color: var(--tui-background-neutral-2);
  animation: tuiSkeletonVibe ease-in-out 1s infinite alternate;
  border-radius: var(--tui-skeleton-radius, 0);
}
.tui-skeleton_light::after {
  background-color: rgba(255, 255, 255, 0.24);
}
.tui-skeleton_rounded::after {
  border-radius: inherit;
}
.tui-skeleton_short::after {
  inline-size: 40%;
}
.tui-skeleton_center::after {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  inline-size: 40%;
}
.tui-skeleton_text::before {
  content: '\a0';
}
.tui-skeleton * {
  opacity: 0;
  visibility: hidden;
